import React, {useState} from 'react'
export default function AccountSetup() {
    const [gender, setGender] = useState()
    function woman() {
        return setGender('female')
    }
    function man() {
        return setGender('male')
    }
    function other() {
        return setGender('other')
    }
    console.log(gender)
    return (
        <div> {gender ? <h2> {gender} </h2> :
            <div className='container'>
                <h1>Giới tính</h1>
                <div className='container-blur'>
                    <div className='box-setting woman' onClick={woman} >
                        <h2>Nữ</h2>
                    </div>
                    <div className='box-setting man' onClick={man}>
                        <h2>Nam</h2>
                    </div>
                    <div className='box-setting lgbt' onClick={other}> 
                        <h2>Khác</h2>
                    </div>
                </div>
            </div> }
        </div>
    )
}
