const initState = {
    projects: [
        {id:1, title:'Tra', content:'chim to '},
        {id:2, title:'Tra-to', content:'chim rat to '}
    ]
}
const projectReducer = (state = initState, action) => {
        switch (action.type) {
            case 'CREATE-PROJECT': 
                console.log('created project ', action.project)
                return state
            case 'CREATE-PROJECT_ERROR': 
                console.log('created project error ', action.err)
                return state
            default:
                return state

        }
        return state
}
export default projectReducer